.container {
    align-content: center;
    display: flex;
    flex-direction: column;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
    justify-content: space-around;
    width: 100%;
    background-color: #101010;
    border: solid thin #fc6621;

}

.top {
    display: flex;
    flex-wrap: wrap-reverse;
    width: auto;
    flex-direction: inherit;
    position: -webkit-sticky;
    position: sticky;
    box-shadow: 1px 1px 1px black;
    top: 0;
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.84);

}

.left, .right {
    flex-wrap: wrap;
    text-align: left;

}

.left {
    flex-basis: 70%;
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.right {
    flex: 1 1;
    display: flex;
    flex-direction: column;

}

.right-sub {
    width: auto;
    margin: 4px 10px 4px 4px;
}

.middle {
    display: flex;
    flex-wrap: wrap;
    height: auto;
    justify-content: center
}

.prestige {
    margin: auto;
}

.logo {
    height: 100px;
}

.card-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.tag-list {
    margin-left: 10px;
}

.test-pageing {
    display: flex;
}

.test-pageing-item:active {
    background-color: gray;
    color: #fc6621;
}

.list{
    display: flex;
    flex-direction: row;
}

.page-labels{
    display: flex;
    margin-bottom: -1px;
}

.pagination-border {
    border: solid thin #fc6621;
    margin-right: 2px;
    margin-left: 1px;
}

.list-nest{
    display: flex;
}
.card{
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
    margin: 10px;
    text-align: center;
    color: #a6a6a6;
    min-height: 190px;
    background-color: #1f1f1f;
    max-height: 390px;
    max-width: 290px;
    min-width: 290px;

}
.card-sub {
    background-color: #2a2a2a;
    margin: 10px;
    text-wrap: normal;
}

.card-top {
    background-color: #2a2a2a;
    color: #fc6621;

}

.card-context {
    -webkit-animation-duration: inherit;
            animation-duration: inherit;
    margin: 5px;
    color: white;
    font-size: 60px;
    height: auto;
    line-height: 111px;
    background-color: #1f1f1f;
    overflow-y: scroll;
}

.table-result{
    text-align: right;
    flex:1 1;
}

.table-line{
    display: flex;
    border-bottom: solid thin #515151;
}

.table{
    margin: 10px;
    height: 300px;
    overflow-y: scroll;
}
#loading {
    -webkit-animation: loading-spinner 2s linear infinite;
            animation: loading-spinner 2s linear infinite;
    font-size: 50px;
}

@-webkit-keyframes loading-spinner {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);

    }
}

@keyframes loading-spinner {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);

    }
}



.graph-container{
    display: flex;
    flex-wrap: wrap;
    margin: 10px;
}



.App {
    display: flex;
    flex-direction: row;
    color: #fc6621;
    justify-content: center;

}

.player-list {
    display: inline;
    margin: 10px;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
}

.player-list:hover {
    -webkit-animation: colorChenger 2s linear infinite;
            animation: colorChenger 2s linear infinite;
}


.statistic-container {
    margin: 5px;
}

.login-step {
    justify-content: center;
    display: flex;
    flex-direction: column;
}


